  position: absolute;
  width: 100%;
  height: 3px;
  content: '';
  background-color: #1f2041;
  left: 0px;
  transition: all 0.3s ease 0s; }

.header-burger__label span {
  top: 13px;
  background-color: #1f2041;
  position: absolute;
  cursor: pointer;
  width: 100%;
  content: '';
  height: 3px;
  left: 0px;
  transition: all 0.3s ease 0s; }

.header-burger__label::before {
  top: 0px; }

.header-burger__label::after {
  bottom: 0px; }

.header-burger__input:checked + .header-burger__label:before {
  transform: rotate(45deg);
  top: 9px; }

.header-burger__input:checked + .header-burger__label:after {
  transform: rotate(-45deg);
  top: 9px; }

.header-burger__input:checked + .header-burger__label span {
  background-color: transparent; }

